import React, { useState, useEffect, useContext } from "react";
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import GoogleLogin from "../AC-LoginBox/components/GoogleSSO";
import history from "../../history";
import {sendFacebookToken} from "../../redux/actions/facebookLoginActions";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { VID, PREVIEW, PROJECT_LINK } from "../../project-config.js";

import {
  LOGIN_SIGNIN_LINK,
  LOGIN_CHECK_LINK,
  LOGOUT_LINK
} from "../../redux/links.js";
import {
  updateLoginInfo,
  updateLoginInfoFb
} from "../../redux/actions/loginActions.js";
import { I18nContext } from "../../i18n/index";

import Loading from "../AC-Loading/Loading.jsx";

import classes from "./Styles/Signin.module.css";

const Signin = () => {
  const dispatch = useDispatch();
  const { langCode, translate } = useContext(I18nContext);

  const [isLogined, setisLogined] = useState(false);
  const [loginInfo, setloginInfo] = useState();
  const [isLoading, setisLoading] = useState(false);

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  );

  const responseGoogle = response => {
    console.log(response);
  };

  function handleSubmit(event) {
    event.preventDefault();
    setisLoading(true);
    var form = new FormData();
    form.append("doSubmit", "Log In");
    form.append("capchacount", "0");
    form.append("loginInput", event.target.email.value);
    form.append("login", event.target.email.value);
    form.append("password", event.target.password.value);
    form.append("logSubmit", "Log In");
    fetch(LOGIN_SIGNIN_LINK.replace("$LANGUAGE", langCode), {
      method: "POST",
      body: form,
      headers: {
        Accept: "*/*",
        credentials: "same-origin"
      },
      mimeType: "multipart/form-data",
      data: form
    })
      .then(res => {
        console.error("deneme", res);
      })
      .then(res => res.json())
      .then(json => console.error("TEST FORM", json))

      // It parses the output
      .catch(function(error) {
        console.log("error---", error);
      })
      .then(() => {
        fetch(LOGIN_CHECK_LINK, {
          headers: {
            Accept: "*/*"
          }
        })
          .then(res => res.json())
          .then(json => {
            console.info("suzuluyorum", json);
            sessionStorage.setItem("UserData", JSON.stringify(json.__Result));
            dispatch(updateLoginInfo(json.__Result));
            setloginInfo(json.__Result);
            setisLoading(false);
            console.error("LOGIN", json);
          });
      });
  }

  const fetchJson = async url => {
    const response = await fetch(url, {
      headers: {
        Accept: "*/*"
      }
    });
    return response.json();
  };

  useEffect(() => {
    if (loginInfo != undefined) {
      if (loginInfo.loggedin == true || loginInfo.userID.length > 1) {
        console.error("LOGIN UPDATED +++", loginInfo);
        setisLogined(true);
      } else {
        console.error("LOGIN UPDATED ---");
        setisLogined(false);
      }
    }
  }, [loginInfo]);

  useEffect(() => {
    fetchJson(LOGIN_CHECK_LINK).then(json => {
      sessionStorage.setItem("UserData", JSON.stringify(json.__Result));

      if (json.__Result.loggedin == true) {
        setloginInfo(json.__Result);
      }
    });
  }, []);

  const responseFacebook = response => {
    console.info("FACEBOOK RESPONSE", response);
    // sessionStorage.setItem("UserData", JSON.stringify(response));
    // dispatch(updateLoginInfoFb(response));
    // setloginInfo(response);
    // setisLoading(false);
    // console.info("after the call23 ", response);
    // console.log(response);
        // "if success"
        if (Object.keys(response).some(res => res === "accessToken")) {
            console.log("FB Works???")
            dispatch(sendFacebookToken(response.accessToken))
            // 1 - Dispatch an action to do the POST to ssoautologin.ajx
            // 2 - Parameters are vid and id_token
            // let id_token = googleUser.getAuthResponse().id_token
            // 3 - on success, update UI and states to have user signed in 
            // We get status data back

        } else {
            console.log("Issues with google sign on")
        }
  };

  return (
    <React.Fragment>
      {isLoading ? (
        <div>
          <div>
            <div>
              <Loading />
            </div>
          </div>
        </div>
      ) : !isLogined ? (
        <div
          // style={{
          //   position: isMobileState ? "relative" : "absolute",
          //   width: isMobileState ? "auto" : "25vw",
          //   top: isMobileState ? "auto" : "-10vh",
          //   right: isMobileState ? "auto" : "-5vw",
          //   marginTop: isMobileState ? "0" : "34px",
          //   marginBottom: isMobileState ? "3vh" : "0"
          // }}
          className={classes.signinWrapper}
        >
          
          <form
            onSubmit={handleSubmit}
            name="loginPopup"
            className={classes.loginPopup}
          >
            <div
              className={classes.errorText}
              id="login_error"
              style={{ display: "block" }}
            />
            <input
              id="email"
              className={classes.mlInput}
              name="loginInput"
              type="email"
              required=""
              placeholder="Enter Your Email"
            />
            <input
              id="password"
              className={classes.mlInput}
              name="password"
              type="password"
              required=""
              placeholder="Enter Your Password"
              autoComplete="on"
            />
            <div className="clearfix" />
            <button type="submit" className={classes.submitButton}>
              Sign in
            </button>
            <div className={classes.or}>Or sign in with</div>
            <div className={classes.loginRegisterbtn}>
              <GoogleLogin />
              <FacebookLogin
                appId="511972516182037"
                autoLoad={false}
                fields="name,email,picture"
                callback={responseFacebook}
                render={renderProps => (
                  <div
                    onClick={renderProps.onClick}
                    className={classes.fbIcon}
                  >
                    <FontAwesomeIcon
                      icon={["fab", "facebook-f"]}
                      // onMouseEnter={toggleHover}
                      // onMouseLeave={toggleHover}
                    />
                  <span style={{marginLeft: "20px"}}>Sign in with Facebook</span>
                  </div>
                )}
              />
            </div>

            <div className={classes.popupForgotW}>
              <a className={classes.popupForgot}>Forgot Password?</a>
              <a className={classes.popupRegister}>Create Account</a>
            </div>
          </form>
        </div>
      ) : null}
    </React.Fragment>
  );
};

export default Signin;
