import React, { useState, useEffect, useRef } from "react";
import { PREVIEW } from "../../../project-config.js";
import history from "../../../history.js";
import classes from "../Styles/SearchBarMobile.module.css";
import { I18nContext } from "../../../i18n/index";

export default function SearchBarMobile() {
  const { translate } = React.useContext(I18nContext);
  const [active, setActive] = useState(false);
  const inputRef = useRef();

  const handleEnterKeyPressed = event => {
    console.info(event.keyCode);
    if (event.keyCode == 13) handleSearchIconClicked();
  };

  const handleSearchIconClicked = () => {
    if (active) {
      let inputValue = inputRef.current.value;
      if (inputValue.trim() !== "") {
        console.info("value", inputValue.trim());
        history.push(`${PREVIEW}/search/${inputRef.current.value}`);
        setActive(false);
      }
    } else {
      setActive(true);
    }
  };

  useEffect(() => {
    if (active) {
      inputRef.current.focus();
    }
  }, [active]);

  return (
    <div
      className={`icon-container ${classes.searchIcon}${
        active ? ` ` + classes.active : ``
      }`}
    >
      <div className={`icon-wrapper ${classes.iconWrapper}`}>
        {active ? (
          <React.Fragment>
            <i onClick={() => setActive(false)} className="material-icons">
              close
            </i>{" "}
            <input
              onKeyDown={handleEnterKeyPressed}
              placeholder={translate("Searchforproduct")}
              ref={inputRef}
              type="text"
              className={classes.input}
            />
          </React.Fragment>
        ) : null}
        <i className="material-icons" onClick={handleSearchIconClicked}>
          search
        </i>
      </div>
    </div>
  );
}
